export default {
	onLoad(option) {
		this.option = option;
		console.log(option)
	},
	data() {
		return {
			dotStyle: false,
			cardCur: 0,
			option: {},
			swiperList: [],
			firstFoo: {},
			secondFoo: {},
			record: [],
			opinion: ''
		}
	},
	methods: {
		previewImage(url){
			let data = url.split(',');
			for (let i=0;i<data.length;i++) {
				data[i] = data[i].replace(/\\/g,'/');
			}
			console.log(data);
			var that = this;
			uni.previewImage({
				urls: data,
				current: data[0]
			});
		},
		ViewImage(e) {
			uni.previewImage({
				urls: this.swiperList,
				current: e.currentTarget.dataset.url
			});
		},
		cardSwiper(e) {
			this.cardCur = e.detail.current
		},
		getFirstInfo() {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/Process/Get_Troubleshooting',
				data: {
					YSCD: self.option.YSCD,
					BranchId: self.option.BranchId
				},
			}).then(data => {
				var [error,res] = data;
				// console.log(res.data)
				if (res.data != '[]') {
					self.firstFoo = JSON.parse(res.data);
					self.firstFoo = self.firstFoo[0]
				}
			})
		},
		getSecondInfo() {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/Process/Get_review',
				data: {
					No: self.option.No,
					BranchId: self.option.BranchId
				},
			}).then(data => {
				var [error,res] = data;
				
				if (res.data != '暂无数据') {
					self.secondFoo = JSON.parse(res.data);
					self.secondFoo = self.secondFoo[0];
					// console.log(self.secondFoo)
					if (self.secondFoo.AQZK == '1') {
						self.secondFoo.AQZK = '轻微隐患'
					} else if (self.secondFoo.AQZK == '2') {
						self.secondFoo.AQZK = '一般隐患'
					}
				}
			})
		},
		getRecord() {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/Process/Get_Processing',
				data: {
					No: self.option.No
				},
			}).then(data => {
				var [error,res] = data;
				
				if (res.data != '暂无数据') {
					console.log(JSON.parse(res.data));
					self.record = JSON.parse(res.data)
				}
			})
		},
		getImage() {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/Process/Get_YHimg',
				data: {
					No: self.option.No
				},
			}).then(data => {
				var [error,res] = data;
				let aa = res.data[0].url;
				self.swiperList = aa.split(',');
				console.log(aa)
			})
		},
		showToast(title) {
			uni.showModal({
				title: '提示',
				content: title,
				showCancel: false,
				success: function(res) {
					if (res.confirm) {}
				}
			});
		},
		commit() {
			if (!this.opinion) {
				this.showToast('请输入归档意见！')
			} else {
				var self = this;
				uni.request({
					url: self.$store.state.urlPrefix + '/Home/File_Cur_Problems',
					data: {
						RYXH: self.$store.state.userInfo.RYXH,
						No: self.option.No,
						CLQK: self.opinion
					},
				}).then(data => {
					var [error,res] = data;
					if (res.data =='ok') {
						uni.showModal({
							title: '提示',
							content: '归档成功!',
							showCancel: false,
							success: function(res) {
								if (res.confirm) {
									
									uni.navigateBack({
										url: '/pages/some/dangerFile/dangerFile'
									})
								}
							}
						});
					} else {
						uni.showModal({
							title: '提示',
							content: '归档失败！',
							showCancel: false,
							success: function(res) {
								if (res.confirm) {
									uni.navigateBack({
										url: '/pages/some/dangerFile/dangerFile'
									})
								}
							}
						});
					}
				})
			}
		}
	},
	mounted() {
		uni.showLoading({
			title:'加载中。。。',
			mask:true
		})
		this.getFirstInfo();
		this.getSecondInfo();
		this.getRecord();
		this.getImage();
		this.$nextTick(function(){
			uni.hideLoading()
		})
	},
}
